.header{
    position: fixed;
    // top:16px;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    z-index: 10;
    display: flex;
    // width: 100%;
    width: 1280px;
    align-items: center;
    justify-content:space-between;
    padding: 6px;
    border-radius: 32px;
    margin: 0 auto;
}

.logo{
    margin-left: 18px;
}
.navigation{
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0;
}
.nav__list{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 32px;  
}
.nav__link{
    display: flex;
    // height: 35px;
    align-items: center;
    justify-content: center;
    // font-weight: 400;
    color: var(--white);
}
.header__button{
    display: flex;
    border: none;
    border-radius: 24px;
    padding: 16px 24px;
    background-color: var(--white);
    align-items: center;
    font-family: 'Fixel Display', sans-serif;
    font-weight: 500;
    font-size: 16px;
    line-height: 120%;
    color: var(--black);
    margin-left: 32px;
}

.header__second{
    background-color: var(--white);
    
}
.nav__link-second{
    color: var(--black);
}
.header__button-second{
    background-color: var(--grey-more-light);
}
.active__link{
    border-radius: 24px;
    padding: 16px;
    height: 43px;
    border: 2px solid var(--grey-more-light);
}
.logo__fixed{
    display: none;
}
